import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import http from '@/common/request.js';
import uniSection from '@/components/uni-section/uni-section.vue';

export default {
	components: {
		uniNavBar,
		uniSection
	},

	data() {
		return {
			// 课程code
			courseCode: '',

			// 课程名称
			courseTitle: '',

			// 课程描述
			courseDesc: '',

			// 课程头部轮播图
			coureLogos: [],

			// 课程教师信息
			courseTeachers: []
		};
	},
	onLoad(o) {
		this.courseCode = o.courseCode;
		this.reloadCourseInfo();
	},

	methods: {
		// 跳转到课程学习页面
		toLearnCourse() {
			let that = this;
			uni.redirectTo({
				url: '../leanCourse/leanCourse?courseCode=' + that.courseCode
			});
		},
		// 获取课程信息
		reloadCourseInfo() {
			let that = this;
			
			uni.showLoading({
				mask:true
			})
			http.request({
				url: `/api/Course/GetCourseInfo?courseCode=${that.courseCode}`
			}).then(r => {
				
				uni.hideLoading()
				
				r = that.fr(r);
				if (r.Code == 200) {
					that.courseTitle = r.Data.main.CourseName;
					that.courseDesc = r.Data.ext.CourseIntroduction;
					that.courseTeachers = r.Data.ext.memberNames;
					// that.coureLogos=[].push(r.Data.)
				}
			});
		},

		// 回退上一页
		back() {
			
			uni.switchTab({
				url:'../myCourse/myCourse'
			})
	
		}
	}
};
